import { useEffect, useState, useRef} from "react";
import { useSelector, useDispatch } from "react-redux";
import { useAuthContext } from "../hooks/useAuthContext";
import { useCollectionDtl } from '../hooks/useCollectionDtl';
import { setUserEmail, setUID, setPostText, setVisible, setDisplayName } from "../store/inputSlice";
import { useFirestore } from "../hooks/useFirestore";
import { setLoginUserInfo } from "../store/loginUserSlice";

function Input(){
    const { isAuthReady, user } = useAuthContext();
    const {documents,error} = useCollectionDtl("UserData",["UID","==",user.uid]) // firebaseì— ì €ì¥ëœ UserData ì»¬ë ‰ì…˜ì—ì„œ ê°€ì ¸ì˜¨ë‹¤!
    const inputState = useSelector((state) => state.inputState)
    const visible = useSelector((state) => state.inputState.visible) // input ëª¨ë‹¬ ì—¬ë¶€
    let dispatch = useDispatch()
    let [showImg, setShowImg] = useState('') // ë¯¸ë¦¬ë³´ê¸° ì´ë¯¸ì§€
    let [saveImg, setSaveImg] = useState('') // ì‹¤ë¬¼ì €ì¥ ì´ë¯¸ì§€
    let [fade, setFade] = useState('') // Animation Style State
    const targetRef = useRef("")

    // ê¸€ ë“±ë¡ ëª¨ë‹¬ on --> ìŠ¤í¬ë¡¤ X
    // ì´ì „ ì‘ì„± ì •ë³´ --> ê¸€, ë¯¸ë¦¬ë³´ê¸° ì´ë¯¸ì§€ ì´ˆê¸°í™”
    useEffect(()=>{
        document.body.style.overflow = visible? 'hidden' : '';
        dispatch(setPostText(''))
        setShowImg('')
        setFade(visible? 'transition-end': '') // ì• ë‹ˆë©”ì´ì…˜ íš¨ê³¼
    },[visible])

    useEffect(()=>{
        // mount, ì´ˆê¸°ë¡œë”© ì™„ë£Œ --> ì‘ì„±ì ì •ë³´ ë¯¸ë¦¬ ì…‹íŒ…
        dispatch(setUserEmail(user.email))
        dispatch(setUID(user.uid))
        dispatch(setDisplayName(user.displayName))

        // unmount ì‹œ ì´ˆê¸°í™”
        return () => {
            dispatch(setVisible(false))
            dispatch(setPostText(''))
            dispatch(setUserEmail(''))
            dispatch(setUID(''))
        }
    },[])

    // ë¡œê·¸ì¸ ì‚¬ìš©ì ì •ë³´ ë°›ì•„ì™€ì„œ, Redux Stateì—ë„ ì €ì¥í•œë‹¤
    useEffect(()=>{
        console.log("ë¡œê·¸ì¸ ìœ ì €(firebase)|",documents)
        if (documents != null && documents.length !== 0) {
            let obj = documents[0]
            delete obj.createdTime // createdTimeì´ ê°ì²´ í˜•íƒœë¼ì„œ non-serializable value ì—ëŸ¬ê°€ ë‚˜ì„œ ì§€ì›Œì¤¬ë‹¤
            dispatch(setLoginUserInfo(obj)) // redux stateì— ì €ì¥
        }
    },[documents])
    
    window.onkeydown = (e) => {
        if(e.key === 'Escape') {
            console.log('ë”¸')
        }
    }
    window.onkeyup = (e) => { 
        if(e.key === 'Escape') {
            console.log('ê¹')
            dispatch(setVisible(false))
        }
    }

    /**************************************************************
     * ê¸€ ì €ì¥
     **************************************************************/
    // ì»¬ë ‰ì…˜ ì´ë¦„ íŒŒë¼ë¯¸í„°ë¡œ ë„£ì–´ì£¼ê¸°, ì €ì¥ì†Œì— í•´ë‹¹ ì´ë¦„ì˜ ì»¬ë ‰ì…˜ìœ¼ë¡œ ì €ì¥ë¨
    const { addDocument, response } = useFirestore("FeedData");
    
    // ì¸í’‹ íƒ€ì…ì— ë”°ë¼ state ê°’ì„ ë³€ê²½
    const onChange = (e) => {
        const inputData = {
            type: e.target.type,
            value: e.target.value
        }
        if (inputData.type === 'text') {
            dispatch(setPostText(inputData.value))
        } else {
            // ì´ë¯¸ì§€ ë¯¸ë¦¬ë³´ê¸°, ì‹¤ë¬¼íŒŒì¼ ì €ì¥
            setPreviewImg(e)
        }
    }

    // ì´ë¯¸ì§€ value ê°’ ë„£ê¸°
    // ì´ë¯¸ì§€ ë¯¸ë¦¬ë³´ê¸°
    const setPreviewImg = (e) => {
        console.log("ì €ì¥í•  íŒŒì¼ -->", e.target.files[0]);

        let reader = new FileReader();
        reader.onload = function(e) {
            // ë¯¸ë¦¬ë³´ê¸°ì— ë³´ì—¬ì¤„ state ë³€ê²½
            setShowImg(e.target.result);
        };
        reader.readAsDataURL(e.target.files[0]);
        setSaveImg(e.target.files[0]);
    }

    // í¼ì´ ì œì¶œë˜ë©´ ì‹¤í–‰ [ê²Œì‹œë¬¼ ë“±ë¡]
    const onSubmit = (e) => {
        e.preventDefault(); // submitì‹œ í˜ì´ì§€ reload ë°©ì§€
        let savedData = {...inputState}
        // ì €ì¥ ì‹œ ì—†ëŠ” ë°ì´í„° ì¶”ê°€ë¡œ ë„£ì–´ì£¼ê¸°
        savedData.peopleWhoLike = []
        savedData.likes = 0
        savedData.peopleWhoReply = []
        savedData.replies = 0
        // íšŒì›ì˜ í”„ë¡œí•„ ì‚¬ì§„ ì •ë³´ë¥¼ ê°€ì ¸ì™€ì„œ ë„£ì–´ì¤Œ, í”„ë¡œí•„ ì‚¬ì§„ì€ íšŒì›ê°€ì…(useSignup) ì‹œ, ê¸°ë³¸ ì´ë¯¸ì§€ë¡œ ë“±ë¡ëœë‹¤
        savedData.profileImage = documents[0].profileImage

        console.log("ê²Œì‹œë¬¼ ë“±ë¡ --> ",savedData);

        // [FireBase ì €ì¥ ë¡œì§]
        addDocument( savedData ,saveImg) // ì €ì¥
        dispatch(setVisible(false))
    }

    return(
        <>
            {
                visible?
                <>
                    {/* ë’¤ì— ìš”ì†Œë“¤ ë®ì–´ì„œ ëª¨ë‹¬ë§Œ ë³´ì´ê²Œ */}
                    <div className={`dimmed-layer ${fade}`}/>

                    <div ref={targetRef} className={`input-area transition-start ${fade}`}>
                        <div className="form-container">
                            <form onSubmit={onSubmit}>
                                <h3>ìƒˆ ê²Œì‹œë¬¼ ë§Œë“¤ê¸°</h3>
                                <div className="form-group">
                                    <input accept="image/jpeg,image/png,image/heic,image/heif,video/mp4,video/quicktime" className="img-input" multiple="" type="file" onChange={onChange} required/>
                                </div>

                                {/* ì´ë¯¸ì§€ ë¯¸ë¦¬ë³´ê¸° */}
                                { showImg.length > 0 && <img src={showImg ?? showImg} alt="" style={{marginBottom : '13px',width : '80%', maxHeight:'200px'}}/>}
                                
                                <div className="form-group">
                                    <input type="text" value={inputState.postText} onChange={onChange} className="form-control" name="postText" placeholder="ë¬¸êµ¬ ì…ë ¥" required/>
                                </div>
                                <input type="submit" className="post-btn" value="ê³µìœ í•˜ê¸°"/>
                                <div className="close-btn">
                                    <CloseBtn onClick={(e)=>{
                                        e.preventDefault()
                                        dispatch(setVisible(false))
                                    }}/>
                                </div>
                            </form>
                        </div>
                    </div>
                </>
                : null
            }
        </>
    )
}

export default Input;


const CloseBtn = (props) => (
    <svg
        // width="800px"
        // height="800px"
        viewBox="0 0 16 16"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        {...props}
    >
    <title>{"close"}</title>
    <path
      fill="#FFFFFF"
      d="M12.78 4.28a.75.75 0 00-1.06-1.06L8 6.94 4.28 3.22a.75.75 0 00-1.06 1.06L6.94 8l-3.72 3.72a.75.75 0 101.06 1.06L8 9.06l3.72 3.72a.75.75 0 101.06-1.06L9.06 8l3.72-3.72z"
    />
  </svg>
);